import React from "react";
import "./imageContent.css";
// @ts-ignore
import Image from "next/image";

interface ImageContentProps {

  /**
   * React className 설정
   */
  className?: string[];
  /**
   * 클릭 핸들러
   */
  onClick?: () => void;

  /**
   * width 설정 
   */
  width: string;
  
  /**
   * height 설정 
   */
  height: string;

  /**
   * src 설정 
   */
  src: string;

  /**
   * alt 설정 
   */
  alt: string;
}

/**
 * Primary UI component for user interaction
 */
export const ImageContent = ({
  className,
  width, height,
  onClick,
  src,
  alt
}: ImageContentProps) => (
  <Image
    // style={style}
    className={`${className.join(" ")}`}
    onClick={onClick}
    aria-hidden="true"
    width={width}
    height={height}
    src={src}
    alt={alt}
  />
);

/**
 * react/require-default-props
 */
ImageContent.defaultProps = {
  className: [],
  // style: { },
  onClick: () => { },
};

export default ImageContent;
